export const pageQuery = graphql`
	query IndexQuery {
		latest: allMarkdownRemark(
			limit: 6
			sort: { fields: [fields___date], order: DESC }
			filter: { frontmatter: { template: { eq: "post" } } }
		) {
			edges {
				node {
					fields {
						slug
						date
					}
					excerpt
					timeToRead
					frontmatter {
						title
						tags
						categories
						thumbnail {
							childImageSharp {
								fixed(width: 150, height: 150) {
									...GatsbyImageSharpFixed
								}
							}
						}
						date
						template
					}
				}
			}
		}
		popular: allMarkdownRemark(
			limit: 7
			sort: { fields: [fields___date], order: DESC }
			filter: { frontmatter: { categories: { eq: "Popular" } } }
		) {
			edges {
				node {
					fields {
						slug
						date
					}
					excerpt
					timeToRead
					frontmatter {
						title
						tags
						categories
						thumbnail {
							childImageSharp {
								fixed(width: 150, height: 150) {
									...GatsbyImageSharpFixed
								}
							}
						}
						date
						template
					}
				}
			}
		}
	}
`
